/* eslint-disable no-alert */
import React from 'react'
import { Link, navigate } from 'gatsby'

import helpers from '../../../helpers'

import logoPath from '../../../images/logoLogin.svg'

import styles from './Login.module.scss'

const { linksPaths } = helpers.constants.links

function Login() {
    return (
        <div className={styles.layout}>
            <header className={styles.header}>
                <Link to={linksPaths.root}>
                    <img src={logoPath} alt="logo" />
                </Link>
            </header>
            <form
                className={styles.form}
                onSubmit={(e) => {
                    e.preventDefault()
                    navigate('/briefcase/')
                }}
            >
                <label className={styles.form__label}>
                    <span>Логин</span>
                    <input className={styles.form__input} type="text" name="login" id="login" />
                </label>
                <label className={styles.form__label}>
                    <span>Пароль</span>
                    <input className={styles.form__input} type="password" name="password" id="password" />
                </label>
                <button className={styles.form__button} type="submit">
                    Войти
                </button>
            </form>
        </div>
    )
}

export default Login
